<template>
  <div class="container">
    <div class="table-container">
      <div class="table-header">
        <a-typography-title class="title" :level="3">Список участников</a-typography-title>
        <div class="buttons-container">
          <a-dropdown :trigger="['click']" v-model:visible="viewColumns">
            <a-button shape="round">
              <template #icon>
                <unordered-list-outlined/>
              </template>
              Выбрать колонки
            </a-button>
            <template #overlay>
              <a-menu>
                <a-menu-item>
                  <a-checkbox-group style="display: flex; flex-direction: column; gap: 6px"
                                    v-model:value="selectedColumns" :options="columns.map(item=>{
                    return {
                      value:item.key,
                      label:item.title
                    }
                  })">{{
                      item.title
                    }}
                  </a-checkbox-group>
                </a-menu-item>
                <a-menu-item>
                  <a-button @click="selectedColumns=[]">Сбросить</a-button>
                </a-menu-item>
              </a-menu>
            </template>
          </a-dropdown>
          <a-dropdown :trigger="['click']" v-if="false">
            <a-button>
              <template #icon>
                <filter-outlined/>
              </template>
              Фильтры
            </a-button>
            <template #overlay>
              <a-menu>
                <a-menu-item>
                  <a href="javascript:;">1st menu item</a>
                </a-menu-item>
                <a-menu-item>
                  <a href="javascript:;">2nd menu item</a>
                </a-menu-item>
                <a-menu-item>
                  <a href="javascript:;">3rd menu item</a>
                </a-menu-item>
              </a-menu>
            </template>
          </a-dropdown>
        </div>
      </div>
      <div class="table-content">
        <a-table :data-source="users" :columns="checkColumns()" class="table">
          <template #bodyCell="{column,record}">
            <div class="" v-if="column.key==='actions'">
              <a-button @click="$router.push({name:'currentMember',params:{id:record.id}})">Редактировать</a-button>
            </div>
            <div class="" v-if="column.key==='sex'">
              {{ record.sex === 'man' ? 'Мужской' : record.sex === 'girl' ? 'Женский' : '' }}
            </div>
            <div class="" v-if="column.key==='registrationDate'||column.key==='birth'">
              {{ record[column.key] ? dayjs(record?.[column?.dataIndex]).format('DD-MM-YYYY'): '' }}
            </div>
          </template>
        </a-table>
      </div>
    </div>
  </div>
</template>

<script>
import {UnorderedListOutlined, FilterOutlined} from "@ant-design/icons-vue";
import dayjs from "dayjs";

export default {
  components: {
    UnorderedListOutlined, FilterOutlined
  },
  computed: {
    users() {
      return this.$store.getters.usersList;
    }
  },
  name: "MembersPage",
  data() {
    return {
      columns: [
        {
          title: 'ФИО',
          key: 'name',
          dataIndex: 'name'
        },
        {
          title: 'Пол',
          key: 'sex',
          dataIndex: 'sex'
        },
        {
          title: 'Дата рождения',
          key: 'birth',
          dataIndex: 'birth'
        },
        {
          title: 'Город',
          key: 'city',
          dataIndex: 'city'
        },
        {
          title: 'Телефон',
          key: 'phone',
          dataIndex: 'phone'
        },
        // {
        //   title: 'Почта',
        //   key: 'email',
        //   dataIndex: 'email'
        // },
        {
          title: 'Дата регистрации',
          key: 'registrationDate',
          dataIndex: 'registrationDate'
        },
        {
          title: 'Действия',
          key: 'actions',
        },
      ],

      dateOptions: {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      },

      viewColumns: false,
      selectedColumns: []
    }
  },
  methods: {
    dayjs,
    checkColumns() {
      if (!this.selectedColumns.length) {
        return this.columns
      } else {
        return this.columns.filter(item => this.selectedColumns.includes(item.key));
      }
    }
  },
  beforeMount() {
    this.$store.dispatch('fetchUsersList', {});
  },
}
</script>

