<template>
  <div class="hackathon-container">
    <div class="hackathon-description">
      <img class="hackathon-preview" :src="hackathon?.previewUrl" alt="" />
      <a-typography-text class="text"
        >{{ hackathon?.description }}
      </a-typography-text>
    </div>
    <hackathon-table
      :can-edit="false"
      :hackathon-id="hackathonId"
    ></hackathon-table>
  </div>
</template>

<script>
import HackathonTable from "@/components/hackathon/HackathonTable.vue";
import { useStore } from "vuex";
import { computed, watch } from "vue";

export default {
  name: "HackathonPage",
  props: ["hackathonId"],
  components: { HackathonTable },
  setup(props) {
    const store = useStore();
    const hackathon = computed(() => store.getters.hackathon);
    store.dispatch("fetchHackathonById", props.hackathonId);
    watch(
      () => hackathon.value,
      (val) => {
        document?.dispatchEvent(
          new CustomEvent("setTitle", { detail: val?.name })
        );
      }
    );
    return {
      hackathon,
    };
  },
};
</script>

